@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  font-display: swap;
  src: local(OpenSans-Light), url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(OpenSans-Regular), url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local(OpenSans-SemiBold), url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(OpenSans-Bold), url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff"); }

.body {
  margin: 0;
  position: relative;
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: black;
  background-color: white; }

.container {
  margin: 0 auto;
  max-width: 1440px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  overflow: hidden; }

.link {
  text-decoration: none; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.header {
  margin-bottom: -62px;
  padding: 21px 280px 0 280px;
  background-color: transparent; }

.header__nav {
  position: relative; }
  .header__nav::before {
    content: "";
    position: absolute;
    bottom: 0;
    display: block;
    height: 2px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.2); }

.header__nav-list {
  padding: 0 19px 9px 19px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between; }

.header__nav-link {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 300;
  line-height: 32px;
  text-align: center;
  letter-spacing: -0.005em; }

.page-header {
  background-color: #1f1f1f;
  background-image: url("../img/background-bike@1x.png");
  background-position: center;
  background-size: 1440px 825px;
  background-repeat: no-repeat; }

.page-header__wrapper {
  padding: 318px 280px 240px 280px;
  display: grid;
  grid-template-columns: 393px 1fr 326px; }

.page-header__logo {
  margin-bottom: 30px; }

.page-header__about h2 {
  margin: 0 0 24px 0;
  font-weight: 700;
  font-size: 46px;
  line-height: 64px;
  letter-spacing: -0.005em;
  color: white; }

.page-header__about p {
  margin: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  letter-spacing: -0.005em;
  color: rgba(255, 255, 255, 0.8); }

.page-header__form {
  grid-column: 3 / 4; }

.page-header__form h3 {
  margin: 0 0 20px 0;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  text-align: center;
  letter-spacing: -0.005em;
  color: white; }

.page-header__form-input {
  margin-bottom: 20px;
  padding-left: 30px;
  width: 100%;
  min-height: 48px;
  border: 1px solid #c9ccd4;
  border-radius: 6px;
  outline: none; }

.page-header__form-button {
  width: 100%;
  min-height: 50px;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px;
  outline: none; }
